import React, { useState, useEffect, useRef } from 'react';
import { useHistory } from "react-router-dom";
import { useTheme, withStyles } from '@material-ui/styles';
import MuiTableHead from "@material-ui/core/TableHead";
import Papa from 'papaparse';
import { Menu, MenuItem } from "@szhsin/react-menu";
import "@szhsin/react-menu/dist/core.css";
import useclasses from './style';
import Users from "./addressBook.json";
import SvgSearch from "../../components/icons/Search";
import SvgImport from "../../components/icons/Import";
import SvgExport from "../../components/icons/Export";
import SvgEdit from "../../components/icons/Edit";
import SvgDelete from "../../components/icons/Delete";
import SvgShare from "../../components/icons/Share";
import SvgCopy from "../../components/icons/Copy";
import ChevronLeft from "../../components/icons/ChevronLeft";
import ChevronRight from "../../components/icons/ChevronRight";
import Dots from "../../components/icons/Dots";

import MuiPaper from "@material-ui/core/Paper";
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import useActiveWeb3React from "hooks/web3-react"

import {
  Box,
  Grid,
  Button,
  Typography,
  InputLabel,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  TableFooter,
  useMediaQuery
} from '@material-ui/core'
import { confirmCorrectAddress } from 'web3/contracts';
import { useDispatch, useSelector } from 'react-redux';
import { setAddressList } from 'store/actions/App';

const TableHead = withStyles((theme) => ({
  root: {
    backgroundColor: "initial"
  }
}))(MuiTableHead);

const Paper = withStyles((theme) => ({
  elevation1: {
    boxShadow: "none"
  }
}))(MuiPaper);

const StyledTableCellLeft = withStyles((theme) => ({
  head: {
    [theme.breakpoints.down('xs')]: {
      background: theme.palette.white_disable,
      borderRadius: "16px 0 0 0",
      padding: "1rem !important",
    }
  },
}))(TableCell);

const StyledTableCellMid = withStyles((theme) => ({
  head: {
    [theme.breakpoints.down('xs')]: {
      background: theme.palette.white_disable,
      padding: "1rem 0 !important",
    }
  },
}))(TableCell);

const StyledTableCellRight = withStyles((theme) => ({
  head: {
    [theme.breakpoints.down('xs')]: {
      background: theme.palette.white_disable,
      borderRadius: "0 16px 0 0",
      padding: "1rem 0px!important",
    }
  },
}))(TableCell);

const ITEM_COUNT = 5;
const EMPTY_IMAGE_URL =
  "https://villagesonmacarthur.com/wp-content/uploads/2020/12/Blank-Avatar.png";

const Address = props => {
  const theme = useTheme();
  const classes = useclasses(theme);
  const [DeleteAccountVisible, setDeleteAccountVisible] = useState(false);
  const [CreateAccountVisible, setCreateAccountVisible] = useState(false);
  const [EditAccountVisible, setEditAccountVisible] = useState(false);
  const [SendCryptoVisible, setSendCryptoVisible] = useState(false);
  const addressList = useSelector(state => state.app.addressList);
  const dispatch = useDispatch();
  const [SelectedId, setSelectedId] = useState(null);
  const [Copied, setCopied] = useState(false);
  const [PageIndex, setPageIndex] = useState(0);
  const [searchTerm, setSearchTerm] = useState("");

  const createNameRef = useRef();
  const createAddressRef = useRef();
  const editNameRef = useRef();
  const editAddressRef = useRef();

  const max792 = useMediaQuery('(max-width:792px)');
  const max576 = useMediaQuery('(max-width:576px)');
  const max375 = useMediaQuery('(max-width:375px)');
  const max400 = useMediaQuery('(max-width:400px)');

  const [isWallet, setIsWallet] = useState(true)
  const [wallet, setWallet] = useState(false)
  const [error, setError] = useState('');
  const { activate } = useActiveWeb3React();
  
  const validateWallet = (event) => {
    if (/^0x[a-fA-F0-9]{40}$/g.test(event)) {
      setWallet(event)
      setError(false)
    } else {
      setWallet('')
      setError('showError')
    }
    return false
  }

  const [Item, setItem] = useState({
    x: 0,
    y: 0,
  });
  const [User, setUser] = useState({
    name: "",
    address: "",
  });
  
  const addUser = (e) => {
    e.preventDefault();
    
    const isAddress = confirmCorrectAddress(createAddressRef?.current?.value);

    try {
      const address = createAddressRef?.current?.value
      const filtered = addressList.find((item) => item.address?.toLowerCase() === address?.toLowerCase());
      if (isAddress && filtered?.address) {
        setError('existingContact')
      } else if ( isAddress && (!User.name && !User.address)) {
        setWallet(address)
        setError(false)
        const user = {name:createNameRef.current.value, address, id: addressList.length + 1}
        localStorage.setItem("addressList", JSON.stringify([...addressList, user]));
        dispatch(setAddressList([...addressList, user]));
        setCreateAccountVisible(false);
      } 
      else if (!isAddress) {
        setWallet('')
        setError('showError')
      }

    } catch (error) {
      setWallet('')
      setError('showError')
    }
    
  };
  const editUser = () => {
      const name = editNameRef?.current?.value;
      const address = editAddressRef?.current?.value;
      if (name) {
      let editContracts = [...addressList];
      editContracts[SelectedId] = {
        ...editContracts[SelectedId],
        name: editNameRef?.current?.value,
        address: editAddressRef?.current?.value
      };
      localStorage.setItem("addressList", JSON.stringify(editContracts));
      dispatch(setAddressList(editContracts));
      setUser({
        name: editNameRef?.current?.value,
        address: editAddressRef?.current?.value
      })
      setEditAccountVisible(false);
    }
  };

  const deleteUser = () => {
    localStorage.setItem("addressList", JSON.stringify(addressList.filter((c, index) => index !== SelectedId)));
    dispatch(setAddressList(addressList.filter((c, index) => index !== SelectedId)));
    setDeleteAccountVisible(false);
  };

  let history = useHistory();

  const onSendCrypto = () => {
    history.push({
      pathname: "/send",
      state: { address: addressList[SelectedId]?.address }
    });
  };

  const onSendNFT = () => {
    history.push("/nfts");
  };

  const openEtherscan = (_address) => {
    var url = `https://etherscan.io/address/${_address}`;
    const newWindow = window.open(url, "_blank", "noopener,noreferrer");
    if (newWindow) newWindow.opener = null;
  };

  const onChange = (e) => {
    // Passing file data (event.target.files[0]) to parse using Papa.parse
    Papa.parse(e.target.files[0], {
      header: true,
      skipEmptyLines: true,
      complete: function (results) {
        localStorage.setItem("addressList", JSON.stringify(results.data))
        dispatch(setAddressList(results.data))
      },
    });
  };
  
  function dealToken(address) {
    if (address && max375) {
      return `...${address && address?.substring(39)}`
    } else if (address && max576) {
      return `${address && address?.substring(0, 4)}...${address?.substring(38)}`
    } else if (address && max792) {
      return `${address && address?.substring(0, 8)}...${address?.substring(34)}`
    }
    return address;
  }

  function dealTokenForSendModal(address) {
    if (address && max576) {
      return `${address.substring(0, 7)}...${address.substring(35)}`
    }
    return address;
  }

  function dealName(name) {
    if (name && (max375 || max400)) {
      return `${name.substring(0, 8)}.. `
    } else {
      return name;
    }
  }

  const MAX_PAGE_NUMBER = Math.ceil(
    addressList.filter((contact) =>
      contact["name"].toLowerCase().includes(searchTerm.toLowerCase())
    ).length / ITEM_COUNT
  );

  const [open, setOpen] = React.useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setDeleteAccountVisible(false);
    setCreateAccountVisible(false);
    setEditAccountVisible(false);
    setSendCryptoVisible(false);
  };

  const [anchorEl, setAnchorEl] = React.useState(null);
  const [openMenu, setOpenMenu] = useState(anchorEl);
  useEffect(() => {
    setOpenMenu(Boolean(anchorEl));
  },[anchorEl])

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setAnchorEl(null);
  };

  useEffect(() => {
    if (Copied)
      setTimeout(() => {
        setCopied(false);
      }, 1500);
  }, [Copied]);
  const handleChange = (event) => {
    // setUser({...User, name:event.target.value});
    setUser({ name: event.target.value });
  };
  return (
    <>
      {DeleteAccountVisible &&
        <MyVerticallyCenteredDialog
          title="Delete Contact"
          handleOpen={handleOpen}
          open={open}
          handleClose={handleClose}
          footer={
            <Box 
              display="flex" 
              flexDirection="column" 
              alignItems="center" 
              style={{ width: '100%' }}
            >
              <Button
                style={{
                  height: "56px", width: "100%",
                  backgroundColor: "#FF8585", borderColor: "#dc3545", marginBottom: "10px"
                }}
                className={classes.dialogBtn}
                onClick={deleteUser}
                fullWidth={true}
              >
                Delete Contact
              </Button>
              <Box className={'classes.buttonGroup'}>
                <Button
                  style={{ height: 56, margin: "10px 0 0 0",backgroundColor: 'transparent' }}
                  className={classes.dialogCancelButton}
                  variant="text"
                  href="#contained-buttons"
                  onClick={() => setDeleteAccountVisible(false)}
                >
                  Cancel
                </Button>
              </Box>
            </Box>
          }
        >
          <Box style={{ margin: "8px 0 " }}>
            You really want to delete{" "}
            <strong> {addressList[SelectedId]?.name} </strong> contact?
          </Box>
        </MyVerticallyCenteredDialog>
      }
      {
        CreateAccountVisible &&
        <MyVerticallyCenteredDialog
          title="Create new contact"
          handleOpen={handleOpen}
          open={open}
          handleClose={handleClose}
          footer={
            <Button fullWidth={true} onClick={(e) => addUser(e)} className={classes.dialogBtn}>
              Create Contact
            </Button>
          }
        >
          <form 
            display="flex" 
            flexdirection="column" 
          >
            <Box 
              style={{ margin: "10px 0" }}
            >
              <input
                type="text" 
                placeholder="Name*"
                ref={createNameRef} 
                className={classes.dialogInput}
                style={{ marginBottom: 28 }}
              />
            </Box>

            <Box
              style={{ marginTop: "1.5rem" }}
            >
              <input 
                type="text" 
                placeholder="Wallet Address" 
                ref={createAddressRef}
                className={(error ===  'showError' || error === 'existingContact') ? classes.dialogInputError :classes.dialogInput}
                style={{border: `1px solid ${ error === ('showError' || 'existingContact')? '#FF8585' : theme.palette.search_bar_border}`}}
              />
              { error === 'showError' ? 
              <Typography className={classes.addressError}>
                Invalid wallet address
              </Typography> : ""}
              { 
                error === 'existingContact' ? 
                <Typography className={classes.addressError}>
                  This is an existing contact / address
                </Typography> : ""
              }
            </Box>
          </form>
        </MyVerticallyCenteredDialog>
      }
      {EditAccountVisible &&
        <MyVerticallyCenteredDialog
          title="Edit Contact"
          handleOpen={handleOpen}
          open={open}
          handleClose={handleClose}
          footer={
            <Button fullWidth={true} onClick={editUser} className={classes.dialogBtn}>
              Save
            </Button>
          }
        >
          <form display="flex" flexdirection="column" noValidate autoComplete="off">
            <Box
              style={{ margin: "10px 0" }}
            >
              <input
                type="text"
                defaultValue={addressList[SelectedId]?.name}
                ref={editNameRef}
                className={classes.dialogInput}
              />
            </Box>

            <Box
              style={{ marginTop: "1.5rem" }}
            >
              <input
                type="text"
                defaultValue={addressList[SelectedId]?.address}
                ref={editAddressRef}
                className={classes.dialogInput}
                disabled
              />
            </Box>
          </form>
        </MyVerticallyCenteredDialog>
      }

      {
        SendCryptoVisible &&
        <MyVerticallyCenteredDialog
          title="Send"
          handleOpen={handleOpen}
          open={open}
          handleClose={handleClose}
          footer={
            <Box display="flex" flexDirection="column" alignItems="center" style={{ width: '100%' }}>
              <Button
                style={{
                  height: "56px", width: "100%",
                  backgroundColor: "#8087ED", marginBottom: "10px"
                }}
                className={classes.dialogBtn}
                onClick={onSendCrypto}
              >
                Send Crypto
              </Button>
              <Button
                style={{
                  height: "56px", width: "100%",
                  backgroundColor: "#8087ED", marginBottom: "10px", marginTop: 24
                }}
                className={classes.dialogBtn}
                onClick={onSendNFT}
              >
                Send NFT's
              </Button>
            </Box>
          }
        >
          <Box>
            <Typography style={{ margin: ".5rem 0 1rem 0" }} className={classes.title18}>Please select what you will send to</Typography>
            <Box display="flex" alignItems="center">
              <img
                src={addressList[SelectedId]?.urlImage || EMPTY_IMAGE_URL}
                alt="Avatar"
                className={classes.image}
              />
              <Box display="flex" alignItems="center" justifyContent="space-between" width="100%">
                <Box display="flex" flexDirection="column" alignItems="flex-start" justifyContent="flex-start">
                  <Typography style={{ paddingLeft: ".5rem" }} className={classes.    subTitle16}>{addressList[SelectedId]?.name}
                  </Typography>
                  <Typography style={{ paddingLeft: ".5rem" }} className={classes.address}>
                      {dealTokenForSendModal(addressList[SelectedId]?.address)}
                    </Typography>
                </Box>
                
                <Box display="flex" alignItems="center" justifyContent="flex-end">
                  <Box className={'classes.share'} display="flex" alignItems="center"  justifyContent="flex-end" 
                  style={{ marginTop: "5px"}}>
                    <IconComponent
                      className="btn-clip"
                      data-bs-toggle="tooltip"
                      data-bs-placement="top"
                      title="Copy to clipboard"
                      onClick={(e) => {
                        navigator.clipboard.writeText(addressList[SelectedId]?.address);
                      }}
                      style={{padding: 0}}
                    >
                      <SvgEdit width={24} height={24}  style={{ cursor: "pointer"}} />
                    </IconComponent>
                  </Box>
                 
                  <Box className={classes.share} display="flex" alignItems="center"  justifyContent="flex-start">
                    <IconComponent
                      onClick={() => openEtherscan(addressList[SelectedId]?.address)}
                    >
                      <SvgShare width={30} height={30}  style={{ transform: "scale(0.8)", cursor: "pointer"}} />
                    </IconComponent>
                  </Box>
                </Box>
              </Box>
            </Box>
          </Box>

        </MyVerticallyCenteredDialog>
      }

      <Box>
        {/* <Meta title='Address Book' description='Address Book' meta={[]} /> */}
        <Box className={classes.main}>
          <Box
            style={{display: 'flex', justifyContent: 'flex-end'}}
          >
            <Box
              className={classes.createContactBtn}
              onClick={() => {
                setUser({ name: "", address: "" });
                setCreateAccountVisible(true);
                handleOpen()
              }}
            >
              <Button className={classes.btnText16} style={{ paddingLeft: "1.5rem", paddingRight: "1.5rem", }}>
                + Create Contact
              </Button>
            </Box>
          </Box>

          {Copied && (
            <Box
              style={{
                transform: `translate(${Item.x - 50}px, ${Item.y - 70}px)`,
              }}
              className={classes.tooltiptext}
              display="flex"
              alignItems="center"
              justifyContent="center"
            >
              <Typography style={{ marginRight: "1rem" }}>Copied!</Typography>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="pl-2"
                width={20}
                height={20}
                fill="none"
                viewBox="0 0 24 24"
                stroke="blue"
                strokeWidth={2}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M5 13l4 4L19 7"
                />
              </svg>
            </Box>
          )}

          <Grid
            container spacing={1}
            style={{ marginTop: "1.5rem", height: 40, marginBottom: "1.5rem" }}
            display="flex" justifyContent="space-between" alignItems="center"
            className={classes.header}
          >
            <Grid item xs={12} sm={6} md={4} lg={4} xl={4} className={classes.input}>
              <Box>
                <SvgSearch
                  style={{
                    position: "absolute",
                    left: 12,
                    top: 16,
                    transform: "scale(0.70)",
                  }}
                />
              </Box>
              <input
                onChange={(e) => {
                  setPageIndex(0);
                  setSearchTerm(e.target.value);
                }}
                placeholder="Search"
                className={classes.inputItem}
              />
            </Grid>
            <Box className={classes.exportImportContainer}>
              <Grid item md={4} lg={4} xl={4}
                style={{ gap: 28, }}
                className={classes.export}
              >
                <Box
                  className={classes.iconText}
                  onClick={() => {
                    objectToCSV(addressList);
                  }}
                >
                  <SvgExport style={{ transform: "scale(0.8)" }} />
                  Export
                </Box>
                <InputLabel className={classes.iconText}>
                  <input type="file" hidden onChange={onChange} />
                  <SvgImport style={{ transform: "scale(0.8)" }} />
                  Import
                </InputLabel>
              </Grid>
            </Box>
          </Grid>

          <TableContainer className={classes.table} component={Paper}>
            <Table className={"table"} aria-label="a dense table">
              <TableHead className={`${classes.tableHead} ${classes.tableHeadBg}`}>
                <TableRow>
                  <StyledTableCellLeft scope="col" className={classes.tableTitle}>
                    Name
                  </StyledTableCellLeft>
                  <StyledTableCellMid scope="col-6"className={classes.tableTitle}>
                    Wallet address
                  </StyledTableCellMid>
                  <StyledTableCellRight></StyledTableCellRight>
                </TableRow>
              </TableHead>
              <TableBody style={{ borderTop: 0 }}>
                {addressList.filter((contact) =>
                  contact["name"]
                    .toLowerCase()
                    .includes(searchTerm.toLowerCase())
                )
                  .slice(PageIndex * ITEM_COUNT, (PageIndex + 1) * ITEM_COUNT)
                  .map((contact, index) => {
                    return (
                      <TableRow key={contact.id} className={classes.tableRow}>
                        <TableCell className={classes.tableText}>
                          <Box display="flex" alignItems="center" >
                            <img
                              src={contact.urlImage ?? EMPTY_IMAGE_URL}
                              alt=""
                              className={classes.image}
                            />
                            <Typography className={classes.tableText}>
                              { dealName(contact.name) }
                            </Typography>
                          </Box>
                        </TableCell>
                        <TableCell className={classes.address}>
                          <Box display="flex" alignItems="center">
                            <Typography className={classes.tableAddress}>
                              { dealToken(contact.address) }
                            </Typography>

                            <IconComponent
                              className="btn-clip"
                              data-bs-toggle="tooltip"
                              data-bs-placement="top"
                              title="Copy to clipboard"
                              onClick={(e) => {
                                setItem({ x: e.clientX, y: e.clientY });
                                setCopied(true);
                                navigator.clipboard.writeText(contact.address);
                              }}
                            >
                              <SvgEdit style={{ cursor: "pointer" }} />
                            </IconComponent>

                            <Box className={classes.share}>
                              <IconComponent
                                onClick={() => openEtherscan(contact.address)}
                              >
                                <SvgShare style={{ transform: "scale(0.8)", cursor: "pointer" }} />
                              </IconComponent>
                            </Box>
                          </Box>
                        </TableCell>

                        <TableCell className={'classes.tableAddress'}>
                          <Box display="flex" alignItems="center" className={classes.hoveredBlock}>
                            <Typography
                              onClick={() => {
                                setSelectedId(
                                  addressList.findIndex(
                                    (data) => data.id == contact.id
                                  )
                                );
                                setSendCryptoVisible(true);
                                handleOpen();
                              }}
                              className={classes.send}
                            >
                              Send
                            </Typography>
                            <IconComponent
                              onClick={() => {
                                setSelectedId(
                                  addressList.findIndex(
                                    (data) => data.id == contact.id
                                  )
                                );
                                setUser({
                                  name: contact.name,
                                  address: contact.address,
                                });
                                setEditAccountVisible(true);
                                handleOpen();
                              }}
                              style={{ paddingLeft: 24, cursor: "pointer" }}
                            >
                              <SvgCopy />
                            </IconComponent>
                            <IconComponent
                              onClick={() => {
                                setSelectedId(
                                  addressList.findIndex(
                                    (data) => data.id == contact.id
                                  )
                                );
                                setDeleteAccountVisible(true);
                                handleOpen();
                              }}
                              style={{ paddingLeft: 24, cursor: "pointer" }}
                            >
                              <SvgDelete />
                            </IconComponent>
                          </Box>

                          <Box className={classes.mobileHoveredBlock}>
                            <Box
                              onClick={() => {
                                setSelectedId(
                                  addressList.findIndex(
                                    (data) => data.id == contact.id
                                  )
                                );
                                setSendCryptoVisible(true);
                                handleOpen();
                              }}
                              style={{ height: 32 }}
                              className={classes.createContact}
                            >
                              <Button
                                className={classes.btnText}
                                style={{}}
                              >
                                Send
                              </Button>
                            </Box>
                            <Box
                              onClick={(e) => {
                                setItem({ x: e.clientX, y: e.clientY });
                              }}
                              className={classes.dotMenu}
                            >
                              <Menu
                                menuStyle={{
                                  position: "fixed",
                                  transform: `translate(${Item.x - 30}px, ${Item.y + 10
                                    }px)`,
                                  padding: 10,
                                  paddingBottom: 0,
                                  paddingTop: 0,
                                }}
                                menuButton={
                                  <Box className={classes.tableIcon}
                                    style={{marginLeft: "12px"}}
                                  >
                                    <Dots />
                                  </Box>
                                }
                              >
                                <MenuItem
                                  style={{ borderBottom: `1px solid ${theme.palette.border_color}`, backgroundColor: 'transparent' }}
                                  onClick={() => {
                                    setSelectedId(
                                      addressList.findIndex(
                                        (data) => data.id == contact.id
                                      )
                                    );
                                    setEditAccountVisible(true);
                                    setUser({
                                      name: contact.name,
                                      address: contact.address,
                                    });
                                    handleCloseMenu();
                                    handleOpen();
                                  }}
                                >
                                  <SvgCopy /> Edit
                                </MenuItem>
                                <MenuItem
                                  style={{ borderBottom: `1px solid ${theme.palette.border_color}`, backgroundColor: 'transparent'}}
                                  onClick={() => openEtherscan(contact.address)}
                                >
                                  <SvgShare width={24} height={24} /> ??
                                </MenuItem>
                                <MenuItem
                                  style={{backgroundColor: 'transparent'}}
                                  onClick={() => {
                                    setSelectedId(
                                      addressList.findIndex(
                                        (data) => data.id == contact.id
                                      )
                                    );
                                    setDeleteAccountVisible(true);
                                    handleCloseMenu();
                                    handleOpen();
                                  }}
                                >
                                  <SvgDelete /> Delete
                                </MenuItem>
                              </Menu>
                            </Box>
                          </Box>
                        </TableCell>
                      </TableRow>
                    );
                  })}
                {addressList.filter((Contact) =>
                  Contact["name"]
                    .toLowerCase()
                    .includes(searchTerm.toLowerCase())
                ).length === 0 && (
                  <TableCell colSpan={4} className={classes.tableText} style={{borderBottom: "none"}}>
                    <Box display="flex" alignItems="center" >
                      <Typography className={classes.noFound}>
                        No results found
                      </Typography>
                    </Box>
                  </TableCell>
                  )}
              </TableBody>
              {addressList.filter((Contact) =>
                  Contact["name"]
                    .toLowerCase()
                    .includes(searchTerm.toLowerCase())
              ).length !== 0 && (
              <TableFooter className={classes.tableFooter}>
                <TableCell style={{ textAlign: "end", borderBottom: "none" }} colSpan={4}>
                  <Box
                    display="flex" alignItems="center"
                    style={{ justifyContent: "flex-end", position: "relative", top: "10px" }}
                  >
                    <Box
                      onClick={() => setPageIndex(Math.max(0, PageIndex - 1))}
                      className={classes.tableIcon}
                    >
                      <ChevronLeft color="white" />
                    </Box>
                    <Typography className={classes.footerText}>
                      {PageIndex + 1}-{MAX_PAGE_NUMBER} of {MAX_PAGE_NUMBER}
                    </Typography>
                    <Box
                      onClick={() =>
                        setPageIndex(
                          Math.min(MAX_PAGE_NUMBER - 1, PageIndex + 1)
                        )
                      }
                      className={classes.tableIcon}
                    >
                      <ChevronRight />
                    </Box>
                  </Box>
                </TableCell>
              </TableFooter>
            )}
            </Table>
          </TableContainer>
        </Box>
      </Box>
    </>
  );
};

export default Address;

function IconComponent({ children, ...rest }) {
  return (
    <span style={{ paddingLeft: 12 }} {...rest} role="button">
      {children}
    </span>
  );
}

const objectToCSV = (data) => {
  const csvRows = [];
  const headers = Object.keys(data[0]);
  csvRows.push(headers.join(","));

  for (const row of data) {
    const values = headers.map((header) => {
      const escaped = ("" + row[header]).replace(/"/g, '\\"');
      return `"${escaped}"`;
    });
    csvRows.push(values.join(","));
  }
  //download
  var csvFile = csvRows.join("\n");
  const blob = new Blob([csvFile], { type: "text/csv" });
  const url = window.URL.createObjectURL(blob);
  const a = document.createElement("a");
  a.setAttribute("hidden", "");
  a.setAttribute("href", url);
  a.setAttribute("download", "Contacts.csv");
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
};

