import { makeStyles } from '@material-ui/styles';
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    marginTop: theme.spacing(4)
  },
  header: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  content: {
    backgroundColor: theme.palette.mainBackground,
    width: '576px',
    height: '100%',
    boxShadow: '0px 0px 20px grey',
  },
  public: {
    '& .ql-align-right': {
      textAlign: 'right'
    },
    '& .ql-align-left': {
      textAlign: 'left'
    },
    '& .ql-align-center': {
      textAlign: 'center'
    },
    color: theme.palette.gray,
    fontSize: '0.9375em',
    fontWeight: 400,
  },
  main: {
    background: theme.palette.main_background,
    width: '100%'
  },
  normalBlock: {
    padding: '30px 30px 30px 30px',
    '& img': {
      width: '100%'
    },
    border: theme.palette.contrastColor
  },
  controlBlock: {
    padding: '30px 30px 70px 30px',
    height: '100%',
    textAlign: 'center',
    border: theme.palette.contrastColor
  },
  buttonBlock: {
    [theme.breakpoints.down('sm')]: {
      display: 'block',
    },
    [theme.breakpoints.up('md')]: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
    color: "white",
    background: theme.palette.controller_color,
    borderRadius: "8px" ,
    padding: "8px 102px",
    height: "56px",
    top: "16px",
    '&:hover': {
      color: "white",
      background: theme.palette.controller_color,
    }
  },

  dialogBtn: {
    [theme.breakpoints.down('sm')]: {
      display: 'block',
    },
    [theme.breakpoints.up('md')]: {
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
    },
    color: "white",
    background: theme.palette.controller_color,
    borderRadius: "8px" ,
    padding: "8px 1rem",
    height: "56px",
    top: "16px",
    '&:hover': {
      color: "white",
      background: theme.palette.controller_color,
    }
  },
  subHeader: {
    color: theme.palette.gray,
    fontSize: '0.9375em',
    fontWeight: 400,
    marginTop: '60px',
    marginBottom: '30px'
  },
  title: {
    fontSize: '36px',
    lineHeight: '32px',
    color: theme.palette.text_main,
  },
  title26: {
    fontWeight: 500,
    fontSize: 26,
    fontStyle: 'normal',
    lineHeight: '36px',
    color: theme.palette.text_main,
    marginBottom: theme.spacing(2)
  },
  title18: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "18px",
    lineHeight: "24px",
    display: "flex",
    alignItems: "center",
    color: theme.palette.text_main,
  },
  subTitle: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    color: theme.palette.text_sub,
    fontSize: '18px',
    lineHeight: '24px',
    marginTop: "8px"
  },
  subTitle16: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "16px",
    lineHeight: "24px",
    display: "flex",
    alignItems: "center",
    color: theme.palette.text_sub,
  },
  createContact: {
    border: `2px solid ${theme.palette.controller_color}`,
    boxSizing: "border-box",
    borderRadius: "8px",
    width: "85px",
    height: "40px",
    wordBreak: "keep-all",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    '&:hover' : {
      backgroundColor: theme.palette.controller_color,
      '& button' : {
        color: 'white',
      },
      
    },
    '& .MuiButton-root': {
    },
    [theme.breakpoints.down('xs')]: {
      width: '70px',
    }
  },
  createContactBtn: {
    border: `2px solid ${theme.palette.controller_color}`,
    borderRadius: '8px',
    boxSizing: 'border-box',
    
    height: '48px',
    wordBreak: "keep-all",
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    '&:hover' : {
      backgroundColor: theme.palette.controller_color,
      '& button' : {
        color: 'white',
      },
      
    },

    '& .MuiButton-root:hover': {
      backgroundColor: 'none !important',
    },

    [theme.breakpoints.up('md')]: {
      maxWidth: '240px',
      position: "absolute",
      width: "240px",
      height: "56px !important",
      top: "5%"
    },
    
    [theme.breakpoints.up('sm')]: {
      position: "absolute",
      height: "40px",
      top: "5%"
    },
    [theme.breakpoints.down('xs')]: {
      width: '100% !important',
      marginTop: "1.5rem"
    }
    
  },
  button: {
    boxSizing: "border-box",
    width: "180px",
    height: "48px",
    border: `2px solid ${theme.palette.controller_color}` ,
    borderRadius: "8px",
    '&:hover' : {
      color: 'white',
      backgroundColor: theme.palette.controller_color
    }
  },
  btnText: {
    fontFamily: "'Lato'",
    fontStyle: 'normal',
    textTransform: 'capitalize',
    fontWeight: '600',
    fontSize: '16px',
    lineHeight: '22px',
    /* identical to box height */
    textAlign: 'center',
    /* Purple/Purple 400 */
    color: theme.palette.controller_color,
    
  },
  btnText16: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 600,
    fontSize: "16px",
    lineHeight: "19px",
    textAlign: "center",
    color: "#8087ED"
  },
  btnOpen: {
    '& .MuiButton-label': {
        textTransform: 'none',
        fontSize: '1.1em',
    },
    '& .MuiButton-containedSecondary:hover': {

    },
    '&:hover': {
        backgroundColor: theme.palette.pink
    },
    padding: '4px',
    border: 'none',
    fontWeight: '300',
    backgroundColor: theme.palette.pink,
    color: theme.palette.black_white,
    marginLeft: '20px',
    marginRight: '20px',
    marginBottom: '20px',
    paddingLeft: '40px',
    paddingRight: '40px',
    width: '400px',
    
  },
  progressContainer: {
    position: 'absolute',
    top: '50%',
    width: 'calc(100% - 50px)',
    display: 'flex',
    justifyContent: 'center',
  },
  progress: {
      color: theme.palette.pink
  },
  tooltip: {
    fontSize: '0.8em'
  },
  addressBookTable: {
    '& tr, td': {
      verticalAlign: 'middle'
    },
    '.szh-menu__item': {
      padding: '10px 5px',
      borderBottom: '1px solid #E0E3FF'
    }
  }, 
  input: {
    position: 'relative',
    height: '40px',
    width: '300px',
  },
  inputItem: {
    border: `1px solid ${theme.palette.search_bar_border}`,
    boxSizing: 'border-box',
    borderRadius: '8px',
    width: '300px',
    backgroundColor: 'transparent',
    paddingLeft: '42px',
    fontFamily: '"Lato", "sans-serif" ',
    outline: 'none',
    height: '40px',
    color: theme.palette.text_main,
    '&:focus': {
      border: `1px solid ${theme.palette.controller_color}`
    },
    '&::placeholder': {
      fontStyle: 'normal',
      fontWeight: '400',
      fontSize: '16px',
      display: 'flex',
      alignItems: 'center',
      color: theme.palette.text_sub,
    },
    [theme.breakpoints.down('xs')]: {
      width: '100% !important',
    }
  },
  export: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
  },

  tooltiptext: {
    visibility: "visible",
    backgroundColor: theme.palette.mainBackground,
    fontSize: "12px",
    lineHeight: "20px",
    color: theme.palette.text_main,
    position: "fixed",
    top: "0",
    left: "0",
    textAlign: "center",
    padding: "10px 12px",
    zIndex: 1,
    border: `1px solid ${theme.palette.border_color}`,
    boxShadow: "0px 2px 6px rgba(63, 66, 113, 0.1)",
    borderRadius: "8px",
    transform: "rotate(-180deg)",
    
    '&::after': {
      content: "''",
      position: "absolute",
      left: "2.5rem",
      bottom: "-6px",
      borderWidth: "8px",
      borderStyle: "solid",
      borderColor: `${theme.palette.mainBackground} ${theme.palette.mainBackground} transparent transparent`,
      borderRadius: "1px",
      transform: "rotate(-225deg)",
      zIndex: 11,
    }
  },
  
  iconText: {
    fontStyle: "normal",
    fontWeight: 600,
    fontSize: "18px",
    fontFamily: "'Lato'",
    lineHeight: "22px",
    display: "flex",
    gap: "8px",
    color: theme.palette.controller_color,
    cursor: "pointer"
  },
  
  tableContainer: {
  },
  table: {
    background: theme.palette.mainBackground,
    border: `1px solid ${theme.palette.border_color}`,
    boxShadow: "0px 4px 28px rgba(63, 66, 113, 0.15)",
    borderRadius: "24px",
    padding: "32px",
    
    '& td': {
      borderBottom: `1px solid ${theme.palette.border_color}`,
      padding: '0'
    },
    '& th': {
      borderBottom: 'none',
    },
    [theme.breakpoints.down('lg')]: {
      '& th': {
        padding: '0'
      },
    },
    [theme.breakpoints.down('xs')]: {
      padding: 0,
      overflow: 'hidden',
      
      '& thead': {
        padding: ".4rem",
        '& .MuiTableRow-root': {
          backgroundColor: theme.palette.wallet_button_color,
        }
      },
      
      '& td': {
        background: theme.palette.topbar_background,
      },

      '& tbody':{
        '& tr:last-child': {
          '& td': {
            borderBottom: "none",
          },
          '& td:first-child':{
            borderBottomLeftRadius: 24, 
          },
          '& td:last-child':{
            borderBottomRightRadius: 24
          }
        },
      },

      '& tfoot':{
        '& td': {
          backgroundColor: 'transparent',
          '& div': {
            justifyContent: "center !important",
            marginBottom: ".5rem", 
            marginTop: ".5rem",
          }
        }
      },
      
      '& $share': {
        display: "none"
      },
      
      backgroundColor: 'transparent !important',
      border: 'none',
      
    }
  },
  tableHead: {
    
  },

  tableHeadBg: {
    
  },

  tableTitle: {
    fontSize: "24px",
    lineHeight: "36px",
    wordBreak: "keep-all",
    color: theme.palette.text_main,
    fontWeight: 400,
    [theme.breakpoints.down('md')]: {
      fontSize: "16px",
      lineHeight: "24px",
    },
    [theme.breakpoints.down('xs')]: {
      fontSize: "14px",
      lineHeight: "17px",
    },
  }, 
  
  tableRow: {
    height: "65px !important",
    '&:hover': {
      backgroundColor: theme.palette.main_background,
      '& $hoveredBlock': {
        visibility: "visible"
      }
    },
  },

  hoveredBlock: {
    visibility: "hidden",
    display: "flex",
    justifyContent: "flex-end",

    [theme.breakpoints.down('sm')]: {
      visibility: "hidden", 
      display: "none",
      '& .hoveredBlock': {
        
      }
    },
  },

  tableText: {
    fontStyle: "normal",
    fontWeight: 400,
    color: theme.palette.text_main,
    fontFamily: "'Lato'",
    fontSize: "18px",
    lineHeight: "24px",
    overflow: "hidden",
    paddingLeft: "0 !important",
    whiteSpace: "nowrap",
    textOverflow: "ellipsis",
    [theme.breakpoints.down('md')]: {
      fontSize: "16px",
      lineHeight: "24px",
    },
    [theme.breakpoints.down('xs')]: {
      fontSize: "14px",
      lineHeight: "20px",
    },
  },
  address: {
    fontSize: "12px",
    lineHeight: "20px",
    color: "rgba(63, 66, 113, 0.5)"
  },

  share: {

  },

  tableAddress: {
    fontWeight: 400,
    fontSize: "16px",
    lineHeight: "24px",
    color: "#797B9C",
    overflow: "hidden",
    whiteSpace: "nowrap",
    textOverflow: "ellipsis",
    [theme.breakpoints.down('md')]: {
      fontSize: "14px",
      lineHeight: "20px",
    },
  }, 
  mobileHoveredBlock: {
    visibility: "hidden",
    display: "none",
    [theme.breakpoints.down('sm')]: {
      visibility: "visible", 
      display: "flex",
      '& .hoveredBlock': {
        display: "none"
      }
    },
  },

  noFound: {
    textAlign: "center !important",
    width: "200%",
    marginTop: "48px",
    marginBottom: "48px",
    color: theme.palette.text_sub,
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "16px",
    lineHeight: "24px",
    borderRadius: "0 0 16px 16px"
  },

  tableFooter: {
    borderTop: "0 !important",
  }, 
  tableIcon: {
    width: "32px",
    height: "32px",
    color: theme.palette.controller_color,
    background: theme.palette.wallet_button_color,
    opacity: 1,
    borderRadius: "8px",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    cursor: "pointer"
  },
 
  footerText: {
    fontSize: "14px",
    lineHeight: "20px",
    display: "flex",
    alignItems: "center",
    color: "#797B9C",
    paddingLeft:"0.3rem",
    paddingRight:"0.3rem"
  },

  image: {
    width: "40px",
    height: "40px",
    borderRadius: "100%",
    margin: "4px"
  },
  send: {
    fontWeight: 600,
    fontSize: "16px",
    lineHeight: "19px",
    cursor: "pointer",
    textAlign: "right",
    color: theme.palette.controller_color,
    [theme.breakpoints.down('xs')]: {
      fontSize: "14px",
      lineHeight: "18px",
    },
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  dialog: {
    '& .MuiDialog-paperWidthSm': {
      borderRadius: 16,
      border: `1px solid ${theme.palette.border_color}`,
      width: 600,
      [theme.breakpoints.down('xs')]: {
        margin: "1rem !important"
      },
    }
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    border: `1px solid ${theme.palette.border_color}`,
    boxShadow: '0px 4px 28px rgba(63, 66, 113, 0.15)',
    borderRadius: "16px",
    padding: theme.spacing(2, 4, 3),
    padding: "2rem 2rem",
    [theme.breakpoints.down('xs')]: {
      padding: ".8rem !important"
    }
  },
  top_divider: {
    height: '1px',
    backgroundColor: theme.palette.border_color,
    marginRight: -32,
    marginLeft: -32,    
  },
  modalDeleteButton: {
    [theme.breakpoints.down('sm')]: {
      display: 'block',
    },
    [theme.breakpoints.up('ms')]: {
      display: 'flex',
      justifyContent: 'center',
    },
    borderRadius: "8px" ,
    height: "56px",
    top: "16px",
    bottom: "16px"
  },

  buttonGroup: {
    '&:hover': {
      '& .MuiButton-root': {
        '&:hover': {
          backgroundColor: 'none !important',
        }
      },
    }
  },

  dialogCancelButton: {
    color: theme.palette.controller_color,
    [theme.breakpoints.down('sm')]: {
      display: 'block',
    },
    [theme.breakpoints.up('ms')]: {
      display: 'flex',
      justifyContent: 'center',
    },
    top: "16px",
  },

  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },

  dialogInput: {
    position: 'relative',
    height: '42px',
    width: '100%',
    border: `1px solid ${theme.palette.search_bar_border}`,
    backgroundColor: 'transparent',
    borderRadius: "8px",
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "14px",
    lineHeight: "20px",
    display: "flex",
    alignItems: "center",
    color: theme.palette.text_main,
    padding: "0 10px",
  },

  dialogInputError: {
    position: 'relative',
    height: '42px',
    width: '100%',
    border: `2px solid #FF8585 !important`,
    backgroundColor: 'transparent',
    borderRadius: "8px",
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "14px",
    lineHeight: "20px",
    display: "flex",
    alignItems: "center",
    color: theme.palette.text_main,
    padding: "0 10px",
    '&:focus': {
      border: "none !important",
    }
    
  },

  exportImportContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    marginRight: "5px",
    [theme.breakpoints.down('xs')]: {
      display: "none",
    },
  },
  top_divider: {
    height: '1px',
    backgroundColor: theme.palette.border_color,
    marginRight: -32,
    marginLeft: -32,    
  },
  addressError: {
    fontFamily: "'Lato'",
    fontStyle: "normal",
    fontWeight: 400,
    fontSize: "12px",
    lineHeight: "20px",
    color: "#FF8585",
    marginTop: "0.3rem"
  },
  dotMenu: {
    '& ul': {
      backgroundColor: theme.palette.main_background,
      border:  `1px solid ${theme.palette.border_color}`,
      boxShadow: "0px 4px 28px rgba(63, 66, 113, 0.15)",
      borderRadius: "8px", 
      padding: ".8rem",
      '& li': {
        padding: ".5rem",
        display:'flex', 
        alignItems: 'center',
        color: theme.palette.text_main,
        '& svg': {
          marginRight: "5px"
        }
        // justifyContent: 'space-between'
      }
    },
    '&:hover': {
      '& li': {
        backgroundColor: "none !important",
      },
    },
    '&:hover': {
      backgroundColor: "none !important",
      '& li': {
      backgroundColor: "none !important",
      },
    },
  }
  
}));

export default useStyles;
